import { getType } from "typesafe-actions";
import { DftAction, dftActions } from "../actions/dft";

const prevUsedFiles = (state = null, action: DftAction) => {
  switch (action.type) {
    case getType(dftActions.getUploadedFiles):
      return "Loading...";
    case getType(dftActions.setPrevUsedFiles):
      return action.payload.prevUsedFiles;
    case getType(dftActions.resetInitializedJobAndPrevUsedFiles):
      return null;
    default:
      return state;
  }
};

export default prevUsedFiles;



