import { getType } from "typesafe-actions";
import { DftAction, dftActions } from "../actions/dft";

const initializedJob = (state = null, action: DftAction) => {
  switch (action.type) {
    case getType(dftActions.setInitializedJob):
      return action.payload.initializedJob;
    case getType(dftActions.resetInitializedJobAndPrevUsedFiles):
      return null;
    default:
      return state;
  }
};

export default initializedJob;



