import { ActionType, createStandardAction } from "typesafe-actions";

const getUploadedFiles = createStandardAction("dft/GET/uploaded-files")();
export interface ISetPrevUsedFiles {
  prevUsedFiles: object | string | null;
}
const setPrevUsedFiles = createStandardAction("dft/SET/prevUsedFiles")<
  ISetPrevUsedFiles
>();
export interface ISubmitOptions {
  options: any;
}
const submitOptions = createStandardAction("dft/POST/conceptual-dft-jobs")<
  ISubmitOptions
>();
export interface ISetInitializedJob {
  initializedJob: object | null;
}
const setInitializedJob = createStandardAction("dft/SET/initializedJob")<
  ISetInitializedJob
>();
export interface ISubmitOneFile {
  file: File;
  jobPk: number;
}
const submitOneFile = createStandardAction("dft/POST/uploaded-files")<
  ISubmitOneFile
>();
export interface ISubmitThreeFiles {
  threeFiles: [File, File, File];
  jobPk: number;
}
const submitThreeFiles = createStandardAction("dft/POST/THREE/uploaded-files")<
  ISubmitThreeFiles
>();
export interface ISubmitOneReusedFile {
  jobPk: number;
  reusedFile: number;
}
const submitOneReusedFile = createStandardAction(
  "dft/POST/uploaded-files/copy"
)<ISubmitOneReusedFile>();
export interface ISubmitThreeReusedFiles {
  jobPk: number;
  threeReusedFiles: [number, number, number];
}
const submitThreeReusedFiles = createStandardAction(
  "dft/POST/THREE/uploaded-files/copy"
)<ISubmitThreeReusedFiles>();
const resetInitializedJobAndPrevUsedFiles = createStandardAction(
  "dft/RESET/initialiedJob&&prevUsedFiles"
)();

export const dftActions = {
  getUploadedFiles,
  resetInitializedJobAndPrevUsedFiles,
  setInitializedJob,
  setPrevUsedFiles,
  submitOneFile,
  submitOneReusedFile,
  submitOptions,
  submitThreeFiles,
  submitThreeReusedFiles
};
export type DftAction = ActionType<typeof dftActions>;



