import { combineReducers } from "redux";
import initializedJob from "./initializedJob";
import isValidAuth from "./isValidAuth";
import prevUsedFiles from "./prevUsedFiles";
import profile from "./profile";

const rootReducer = combineReducers({
  initializedJob,
  isValidAuth,
  prevUsedFiles,
  profile
});
export default rootReducer;



