import { all, call, put, takeLatest } from "redux-saga/effects";
import { ActionType, getType } from "typesafe-actions";
import { apiActions, dftActions } from "../actions";
import { queryResource } from "./apiWatcher";

function* getUploadedFiles() {
  const prevUsedFiles = yield call(
    queryResource,
    apiActions.start({
      method: "GET",
      resourceID: "uploaded-files"
    })
  );
  yield put(dftActions.setPrevUsedFiles({ prevUsedFiles }));
}
function* submitOptions(action: ActionType<typeof dftActions.submitOptions>) {
  const initializedJob = yield call(
    queryResource,
    apiActions.start({
      data: action.payload.options,
      method: "POST",
      resourceID: "conceptual-dft-jobs"
    })
  );
  yield put(dftActions.setInitializedJob({ initializedJob }));
}
function* uploadFile(file: File, jobPk: number) {
  const data = new FormData();
  data.append("job", jobPk.toString());
  data.append("upload", file);
  const uploadedFile = yield call(
    queryResource,
    apiActions.start({
      data,
      method: "POST",
      resourceID: `uploaded-files`
    })
  );
  return uploadedFile;
}
function* submitConceptualDftJob(jobPk: number) {
  const conceptualDftJob = yield call(
    queryResource,
    apiActions.start({
      data: {
        status: "E" // FIXME: Change to 'S' to actually submit this job
      },
      method: "PATCH",
      resourceID: `conceptual-dft-jobs/${jobPk}`
    })
  );
  if (conceptualDftJob) {
    yield put(dftActions.resetInitializedJobAndPrevUsedFiles());
    // TODO: redirect to somewhere
  }
}
function* submitOneFile(action: ActionType<typeof dftActions.submitOneFile>) {
  const uploadedFile = yield call(
    uploadFile,
    action.payload.file,
    action.payload.jobPk
  );
  if (uploadedFile) {
    yield call(submitConceptualDftJob, action.payload.jobPk);
  }
}
function* submitThreeFiles(
  action: ActionType<typeof dftActions.submitThreeFiles>
) {
  const uploadedFile1 = yield call(
    uploadFile,
    action.payload.threeFiles[0],
    action.payload.jobPk
  );
  if (uploadedFile1) {
    const uploadedFile2 = yield call(
      uploadFile,
      action.payload.threeFiles[1],
      action.payload.jobPk
    );
    if (uploadedFile2) {
      const uploadedFile3 = yield call(
        uploadFile,
        action.payload.threeFiles[2],
        action.payload.jobPk
      );
      if (uploadedFile3) {
        yield call(submitConceptualDftJob, action.payload.jobPk);
      }
    }
  }
}
function* reuseFile(filePk: number, jobPk: number) {
  const reusedFile = yield call(
    queryResource,
    apiActions.start({
      method: "POST",
      query: `?toJob=${jobPk}`,
      resourceID: `uploaded-files/${filePk}/copy`
    })
  );
  return reusedFile;
}
function* submitOneReusedFile(
  action: ActionType<typeof dftActions.submitOneReusedFile>
) {
  const reusedFile = yield call(
    reuseFile,
    action.payload.reusedFile,
    action.payload.jobPk
  );
  if (reusedFile) {
    yield call(submitConceptualDftJob, action.payload.jobPk);
  }
}
function* submitThreeReusedFiles(
  action: ActionType<typeof dftActions.submitThreeReusedFiles>
) {
  const reusedFile1 = yield call(
    reuseFile,
    action.payload.threeReusedFiles[0],
    action.payload.jobPk
  );
  if (reusedFile1) {
    const reusedFile2 = yield call(
      reuseFile,
      action.payload.threeReusedFiles[1],
      action.payload.jobPk
    );
    if (reusedFile2) {
      const reusedFile3 = yield call(
        reuseFile,
        action.payload.threeReusedFiles[2],
        action.payload.jobPk
      );
      if (reusedFile3) {
        yield call(submitConceptualDftJob, action.payload.jobPk);
      }
    }
  }
}
function* dftWatcher() {
  yield all([
    takeLatest(getType(dftActions.getUploadedFiles), getUploadedFiles),
    takeLatest(getType(dftActions.submitOptions), submitOptions),
    takeLatest(getType(dftActions.submitOneFile), submitOneFile),
    takeLatest(getType(dftActions.submitThreeFiles), submitThreeFiles),
    takeLatest(getType(dftActions.submitOneReusedFile), submitOneReusedFile),
    takeLatest(
      getType(dftActions.submitThreeReusedFiles),
      submitThreeReusedFiles
    )
  ]);
}
export default dftWatcher;



