import { delay } from "redux-saga";
import { all, call, put, takeEvery, takeLatest } from "redux-saga/effects";
import { ActionType, getType } from "typesafe-actions";
import { apiActions, IApiStart } from "../actions";
import apiRequest from "../api";
import auth from "../auth";

function* getApiResponse(args: IApiStart) {
  const { method, resourceID, data, query = "" } = args;
  const { getAccessToken } = auth;
  const API_URL = process.env.REACT_APP_API_URL;
  const url = `${API_URL}/${resourceID}/${query}`;
  const apiResponse = yield call(apiRequest, {
    accessToken: getAccessToken(),
    data,
    method,
    url
  });
  return apiResponse;
}

function* queryApi(args: IApiStart) {
  for (let i = 0; i < 5; i++) {
    try {
      const apiResponse = yield call(getApiResponse, args);
      return apiResponse;
    } catch (error) {
      if (i < 4) {
        const { method, resourceID } = args;
        yield put(
          apiActions.retry({
            method,
            payload: error.message,
            resourceID
          })
        );
        yield call(delay, 2000);
      }
    }
  }
  // attempts failed after 5 attempts
  throw new Error("API request failed");
}

export function* queryResource(action: ActionType<typeof apiActions.start>) {
  const { method, resourceID } = action.payload;
  try {
    const apiResponse = yield call(queryApi, action.payload);
    yield put(apiActions.success({ method, resourceID, payload: apiResponse }));
    return apiResponse;
  } catch (error) {
    yield put(
      apiActions.error({
        method,
        payload: error.message,
        resourceID
      })
    );
    // throw error;
    return null;
  }
}

function* queryEmail() {
  yield call(
    queryResource,
    apiActions.start({ method: "GET", resourceID: "user-email" })
  );
}

function* apiWatcher() {
  yield all([
    takeEvery(getType(apiActions.start), queryResource),
    takeLatest(getType(apiActions.queryEmail), queryEmail)
  ]);
}
export default apiWatcher;



