import { ActionType, createStandardAction } from "typesafe-actions";
export interface IApiStart {
  method: "POST" | "GET" | "PUT" | "PATCH" | "DELETE";
  resourceID: string;
  data?: object;
  query?: string;
}
const start = createStandardAction("api/START")<IApiStart>();

export interface IApiResponse {
  method: "POST" | "GET" | "PUT" | "PATCH" | "DELETE";
  resourceID: string;
  payload: string | object;
}
const retry = createStandardAction("api/ERROR/RETRY")<IApiResponse>();
const error = createStandardAction("api/ERROR/FINAL")<IApiResponse>();
const success = createStandardAction("api/SUCCESS")<IApiResponse>();

const queryEmail = createStandardAction("api/EMAIL")();

export const apiActions = {
  error,
  queryEmail,
  retry,
  start,
  success
};
export type ApiAction = ActionType<typeof apiActions>;



