import Collapse from "@material-ui/core/Collapse";
import Divider from "@material-ui/core/Divider";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import AddCircleIcon from "@material-ui/icons/AddCircle";
import EmailIcon from "@material-ui/icons/Email";
import GetAppIcon from "@material-ui/icons/GetApp";
import HomeIcon from "@material-ui/icons/Home";
import InfoIcon from "@material-ui/icons/Info";
import WebIcon from "@material-ui/icons/Web";
import ZoomInIcon from "@material-ui/icons/ZoomIn";
import React from "react";
import { Link } from "react-router-dom";

const HomeLink: React.SFC = props => <Link to="/home" {...props} />;
const SubmitDftLink: React.SFC = props => <Link to="/submit-dft" {...props} />;
const ProfileLink: React.SFC = props => <Link to="/profile" {...props} />;

const listItem = (Icon: any, text: string, component?: any) => (
  <ListItem button={true} component={component}>
    <ListItemIcon>
      <Icon />
    </ListItemIcon>
    <ListItemText primary={text} />
  </ListItem>
);

const homeListItem = listItem(HomeIcon, "Home", HomeLink);
const submitDftListItem = listItem(AddCircleIcon, "Submit DFT", SubmitDftLink);
const profileListItem = listItem(WebIcon, "Profile", ProfileLink);
const aboutHortonListItem = listItem(InfoIcon, "About HORTON");
const learnMoreListItem = listItem(ZoomInIcon, "Learn more");
const emailUsListItem = listItem(EmailIcon, "Email us");

export interface ISidebarListProps {
  classes: any;
  a2hsPrompt: boolean;
  addToHomeScreen: any;
  isValidAuth: boolean;
}
const SidebarList: React.SFC<ISidebarListProps> = ({
  classes,
  a2hsPrompt,
  addToHomeScreen,
  isValidAuth
}) => (
  <div>
    <div className={classes.toolbar} />
    {isValidAuth && (
      <div>
        <Divider />
        <List>
          {homeListItem}
          {profileListItem}
          {submitDftListItem}
        </List>
      </div>
    )}
    <Divider />
    <List>
      {aboutHortonListItem}
      {learnMoreListItem}
      {emailUsListItem}
    </List>
    <Divider />
    <Collapse in={a2hsPrompt}>
      <List>
        <ListItem button={true} onClick={addToHomeScreen}>
          <ListItemIcon>
            <GetAppIcon />
          </ListItemIcon>
          <ListItemText primary="Add to home" />
        </ListItem>
      </List>
    </Collapse>
  </div>
);
export default SidebarList;



